.App{
  font-family: sans-serif;
  text-align: center;
}
:root{
  font-size: 20px;
  
}

.heading{
  color: crimson;
}
.Plan{
  display: flex;
  justify-content: space-between;
  margin: 50px;
  padding: 30px;
  padding-left: 20px;
  
}
.Card {
  align-content: center;
  width: fit-content;
  background-color: white;
  border-style:dotted;
  border-radius: 50px;
  padding: 16px;
  margin-bottom: 8px;
  background-color: rgb(245, 221, 221);
  font-weight:bold;
}


body {
  background-color:rgb(49, 3, 43);

} 

.buy:hover{
  background-color: #05bed0;
  border-color: #05a58ca0;
}

.features{
  list-style: none;
  text-align:left;
  border-top: 1px solid rgb(212,209,209);
  padding-top: 0.73rem;
}

.features> li{
  margin-bottom: 0.5rem;
  font-size: 0.8rem;
}
.features > li::marker{
  content: "✓";
  color: rgba(8, 194, 17, 0.849);
  font-weight: bold;
}
.Card:nth-child(1) .features> li:nth-child(n+5)::marker{
  content: "✗";
  color: red;
  font-weight: bold;
  
}
.Card:nth-child(1) .features> li:nth-child(n+5){
  font-family:strikethrough;
  font-weight: normal;
}
.Card:nth-child(2) .features> li:nth-child(n+8)::marker{
content: "✗";
color: red;
}
.Card:nth-child(2) .features> li:nth-child(n+8){
 font-family:strikethrough;
 font-weight: normal;
}

.buy {
  width: 100%;
  background-color: #0d6ef0;
  border-color: #4f6d94d3;
  color: white;
  border-radius: 1000px;
  padding: 10px;
  font-size: 12px;
  letter-spacing: 4px;
  opacity: 4px;
}

.price {
  font-size: 2rem;
  margin:1rem 0;
  text-align: center;
}

.price-duration {
  font-size: 0.6rem;
  text-align: center;
}
.plan-name {
  color: slategray;
  font-size: 0.7rem;
  letter-spacing: 1.5px;
  text-align: center;
}